//
// forest
//
#forest{
	.share {
		padding: rem(30px) 0 rem(100px);
	}
	.forestLocation {
		position:relative;
		width:100%;
		height:720px;
		background:#f2f1f1 url('../img/forest/map.png') no-repeat 0% 50%/contain;
		.location_pointer {
		position:relative;	
			.map_pointer {
				width: rem(15px);
				height: rem(15px);
				background: url('data:img/png;base64,') no-repeat 50% 50%/cover;
				position: absolute;
				&.map_point1 {
					top:rem(180px);
					left:rem(300px);
				}
				&.map_point2 {
					top:rem(300px);
					left:rem(350px);
				}
				&.map_point3 {
					top:rem(380px);
					left:rem(380px);
				}
				&.map_point4 {
					top:rem(340px);
					left:rem(420px);
				}
				&.map_point5 {
					top:rem(340px);
					left:rem(565px);
				}
				&.map_point6 {
					top:rem(365px);
					left:rem(565px);
				}
			}
		}
		.location_desc {
			.desc {
				position:absolute;
				border-radius:10px;
				background-color: $orange;	
				width: rem(300px);
				height: rem(90px);
				opacity: 0;
				visibility: hidden;
				&:before {
					background-color: $orange;
					width: 1px;
					height: rem(36px);
					content:'';
					display: block;
					position:absolute;
					left:50%;
					margin-left: rem(-0.5px);
					bottom: rem(-36px) 
				}
				.numText {
					width: rem(54px);
					color:$text-color-white;
					font-size: rem(32px);
					line-height: rem(60px);
					display: inline-block;
					text-align:center;
				}
				.text {
					width: rem(238px);
					height: rem(50px);
					color:$text-color-gray;
					background-color: $text-color-white;
					border-radius: 10px;
					display: inline-block;
					padding: rem(14px) 0;
					margin: rem(5px) rem(5px) rem(5px) 0;
				}		
				.tree {
					position:absolute;
					top:rem(-64px);
					left:50%;
					margin-left: rem(-82.5px);
					background: url('data:img/png;base64,') no-repeat 50% 50%/cover;
					width:rem(165px);
					height:rem(64px);
					opacity: 0;
					visibility: hidden;
				}
			}
		}
		.map_desc {
			margin-left:60%;
			width: rem(392px);
			padding-top: rem(100px);
			.section-header {
				.heading {
					color: $text-color-gray;
					text-align: center;
					font-size: rem(24px);
					.text-color-orange {
						font-weight: 700;
					}
				}
				.btn-corner-rounded {
					width: rem(280px);
					margin: rem(20px) auto;
				}
			}
			.owl-carousel {
				width: rem(392px);
				.img {
					width: rem(392px);
					height: rem(290px);
					display:block;
				}
				.desc {
					padding-top: rem(50px);
					color: $text-color-gray;
					text-align: center;
					font-size: rem(18px);
					line-height: rem(33px);
				}
				.forest-item1 {
					.img {
						background: url('../img/forest/map_location-1.jpg') no-repeat 50% 50%/cover;
					}
				}
				.forest-item2 {
					.img {
						background: url('../img/forest/map_location-2.jpg') no-repeat 50% 50%/cover;
					}
					
				}
				.forest-item3 {
					.img {
						background: url('../img/forest/map_location-3.jpg') no-repeat 50% 50%/cover;
					}
				}
				.owl-dots {
					top: rem(305px);
					text-align: center;
					.owl-dot {
						border-radius: 50%;
						background-color: rgb(0, 0, 0);
						opacity: 0.2;
						width: 12px;
						height: 12px;
					 	&.active {
							background-color: rgb(243, 115, 33);
							width: 32px;
							height: 12px;
							border-radius:20px;
							opacity: 1;
					 	}
					}
				}
			}
		}
	}
	.forestResult {
		width:100vw;
		height: calc(100vw * 607 / 1600);
		background-color: rgb(255,255,255);
		z-index:31;
		background: url('../img/forest/bg-forest_result.jpg') 50% 50%/cover;
	}
}
